body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content{
  min-height: calc(100vh - 70px - 64px);
}

.preloader-wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 70px auto;
}
.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}
.card {
  display: flex;
  flex-direction: column;
}
img,
iframe {
  max-width: 100%;
}
iframe {
  width: 600px;
  height: 350px;
}
nav .brand-logo{
  font-weight: bold;
  color: white;
  font-size: 2em;
  letter-spacing: 2px;
}
nav ul a{
  font-size: 1.2em;
  color:white;
}
.btn, .btn-large, .btn-small{
  background-color: #c2185b ; 
}
.btn:hover {
  background-color:#ad1457 ;
  transition: 0.7s;
}

.btn:focus{
  background-color:#880e4f  ;
}

.page-footer{
  background-color: #c2185b  ;
}

.phoneNumb{
  margin: 50px 0 100px;
}
.recipe_text{
  font-weight: bold;
}
.text-about{
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}
.about_form{
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.about_all{
  height: 200px;
  width: 400px;
  margin: 0 auto;
  border: solid 3px #c2185b;
}
.btn_about{
  margin-top: 30px;
  max-width: 300px;
  width: 100%;
}
.recipe_text{
  margin-bottom: 50px;
}

.btn, .btn-large, .btn-small{
  margin-top: 10px;
  margin-bottom: 10px;
}